</template>
<script>
export default {
  data() {
    return {
      on: true,
      items: []
    };
  },
  methods: {
    async ferch() {
      const res = await this.$http.get("/js/mvvm");
      this.items = res.data;
       this.on = false;
    }
  },
  created() {
    this.ferch();
  }
};
</script>
